
.header {
    margin-left: 40px;
}
.show-animate {
  text-align: center;
}
.btn {
  width: 300px;
  margin-bottom: 30px;
  margin-left: 40px;
}
.button {
  float: right;
}
.animate {
  width: 50px;
  margin-bottom: 20px;
  color: #fff;
  background: seagreen;
  border-radius: 3px;
  -webkit-animation: animate 5s infinite;
          animation: animate 5s infinite;
}
@-webkit-keyframes animate
{
from {
    width: 50px;
}
to {
    width: 300px;
}
}
@keyframes animate
{
from {
    width: 50px;
}
to {
    width: 300px;
}
}





